.carousel-container {
  overflow-x: auto;
  scroll-behavior: smooth;
  width: 80%; /* sau orice procentaj sau dimensiune doriți */
  margin: 0 auto; /* Acesta este stilul cheie pentru centrare */
  position: relative;
}

.carousel-slides {
  display: flex;
  transition: transform 0.5s ease;
  will-change: transform;
}

.carousel-slide {
  flex: 0 0 auto; /* Permite ca fiecare slide să își mențină propria lățime */
  white-space: normal; /* Asigură-te că textul din slide-uri nu este forțat să stea pe o singură linie */
  margin-right: 10px; /* Adaugă spațiu între slide-uri */
}
