.header-hero {
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.header-hero img {
  object-fit: contain;
}

.header-hero-text {
  position: absolute;
  color: var(--clr-linkshover);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.gradient-text {
  background: linear-gradient(
    90deg,
    #cb1333,
    #de45c1,
    #b96be2,
    #84a9e0,
    #23d43b,
    #d0bb08,
    #eb6d17
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
}

.header-hero-heading {
  font-size: 56px;
  line-height: 60px;
  margin-top: 50px;
}

.header-subheading {
  font-size: 28px;
  font-weight: 400;
  line-height: 30px;
  margin-top: -20px;
}

.header-links {
  display: flex;
  gap: 40px;
  font-size: 21px;
  color: #2997ff;
}

.header-icon {
  font-size: 24px;
}

.header-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  cursor: pointer;
}

.header-link a {
  text-decoration: none;
  color: #2997ff;
}

.header-link:hover > a {
  text-decoration: underline;
}
