.navbar {
  background-color: var(--clr-navbar);
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 44px;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1024px;
  width: 100%;
}

.navbar-icons {
  color: var(--clr-navlinks);
  font-size: var(--fs-navicons);
}

.navbar-links {
  display: flex;
  list-style: none;
  font-size: var(--fs-navlinks);
  justify-content: space-between;
  width: 100%;
}

.navbar-link a {
  text-decoration: none;
  color: var(--clr-navlinks);
}

.navbar-utilities {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  margin-left: 40px;
}
