.productCard-hero {
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.productCard-hero img {
  width: 100%;
  object-fit: cover;
  min-height: 500px;
}

.productCard-hero-text {
  position: absolute;
  color: var(--clr-linkshover);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.productCard-heading {
  font-weight: 700;
  font-size: 40px;
  text-align: center;
}

.productCard-subheading {
  margin-top: -20px;
  max-width: 400px;
  text-align: center;
  font-weight: 400;
  font-size: 21px;
}

.productCard-links {
  display: flex;
  gap: 20px;
}

.text-light h1,
.text-light h2 {
  color: white;
}

.text-dark h1,
.text-dark h2 {
  color: black;
}

.product-icon {
  font-size: 35px;
}

.productCard-para {
  margin-top: -30px;
  margin-bottom: 30px;
  color: #d11718;
}

.productCard-link a {
  text-decoration: none;
}

.productCard-link a:hover {
  text-decoration: underline;
}
